import React, { useCallback, useRef, useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

const NewChat = () => {
    const { apply_id } = useParams();

    const [msg, setMsg] = useState('');
    const [name, setName] = useState('');
    const [chatt, setChatt] = useState([]);
    const [chkLog, setChkLog] = useState(false);
    const [socketData, setSocketData] = useState();

    const ws = useRef(null);

    const msgBox = chatt.map((item, idx) => (
        <div key={idx} className={item.name === name ? 'me' : 'other'}>
            <span>
                <b>{item.name}</b>
            </span>{' '}
            [ {item.date} ]<br />
            <span>{item.msg}</span>
        </div>
    ));

    useEffect(() => {
        console.log('Chatt state updated:', chatt);
        if (socketData !== undefined) {
            const tempData = chatt.concat(socketData);
            setChatt(tempData);
        }
    }, [socketData, chatt]);

    const onText = (event) => {
        setMsg(event.target.value);
    };

    const webSocketLogin = useCallback(() => {
        ws.current = new WebSocket(`ws://localhost:8080/socket/chatt/${apply_id}`);

        ws.current.onopen = () => {
            console.log('WebSocket connection opened');
        };

        ws.current.onmessage = (message) => {
            console.log('WebSocket message received:', message.data);
            const newMessage = JSON.parse(message.data);
            setChatt((prevChatt) => [...prevChatt, newMessage]);
        };


        ws.current.onclose = (event) => {
            console.log('WebSocket connection closed:', event);
        };

        ws.current.onerror = (error) => {
            console.error('WebSocket error:', error);
        };
    }, [apply_id]);

    const send = useCallback(() => {
        if (!chkLog) {
            if (name === '') {
                alert(' .');
                document.getElementById('name').focus();
                return;
            }
            webSocketLogin();
            setChkLog(true);
        }

        if (msg !== '') {
            const data = {
                name,
                msg,
                date: new Date().toLocaleString(),
            };

            const temp = JSON.stringify(data);

            if (ws.current.readyState === WebSocket.OPEN) {
                ws.current.send(temp);
            } else {
                console.warn('WebSocket is not open');
            }
        } else {
            alert(' .');
            document.getElementById('msg').focus();
            return;
        }
        setMsg('');
    }, [chkLog, msg, name, webSocketLogin]);

    return (
        <div id="chat-wrap">
            <div id="chatt">
                <h1 id="title">WebSocket Chatting</h1>
                <br />
                <div id="talk">{msgBox}</div>
                <input
                    disabled={chkLog}
                    placeholder=" ."
                    type="text"
                    id="name"
                    value={name}
                    onChange={(event) => setName(event.target.value)}
                />
                <div id="sendZone">
                    <textarea
                        id="msg"
                        value={msg}
                        onChange={onText}
                        onKeyDown={(ev) => {
                            if (ev.keyCode === 13) {
                                send();
                            }
                        }}
                    ></textarea>
                    <input type="button" value="" id="btnSend" onClick={send} />
                </div>
            </div>
        </div>
    );
};

export default NewChat;
