import { useState } from "react";
import { RegisterTextField } from "./components/RegisterTextField";
import { PasswordTextField } from "./components/RegisterTextField";
import Select, { SelectChangeEvent } from '@mui/material/Select';
import { Button } from "../../components/common";
import { RegisterButton } from "../../components/common";
import { FormControl, InputLabel, MenuItem } from "@mui/material";
import CheckIcon from '@mui/icons-material/Check';
import { useNavigate } from 'react-router-dom';
export function Register() {
    const [email, setEmail] = useState('');
    const [authCode, setAuthCode] = useState('');
    const [code, setCode] = useState('');
    const [isCodeEqual, setIsCodeEqual] = useState(false);
    const [userId, setUserId] = useState('');
    const [isIdDuplicate, setIsIdDuplicate] = useState(false);
    const [isIdDuplicateButton, setIsIdDuplicateButton] = useState(0);
    const [password, setPassword] = useState('');
    const [passwordcheck, setPasswordcheck] = useState('');
    const [nickname, setNickname] = useState('');
    const [isNicknameDuplicate, setIsNicknameDuplicate] = useState(false);
    const [isNicknameDuplicateButton, setIsNicknameDuplicateButton] = useState(0);
    const [gender, setGender] = useState('');
    const [status, setStatus] = useState(true);
    const navigate = useNavigate();
    const handleEmailConfirm = async (e: React.FormEvent) => {
        e.preventDefault();
        try {
            const response = await fetch('http://aniroomi-env.eba-rj7upyms.ap-northeast-2.elasticbeanstalk.com/mailConfirm', {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                email: email,
              }),
            });
      
            if (response.ok) {
              //     
              const data = await response.text();
              if(data != ''){
                setAuthCode(data);
              }
              console.log({authCode});
            } else {
              //    
              console.error('Error during mailConfirm1:', response.statusText);
            }
          } catch (error) {
            console.error('Error during mailConfirm2:', error);
          }
        };
    const handleCodeCompare = async (e: React.FormEvent) => {
        if(code === authCode){
            setIsCodeEqual(true);
        }else{
            setIsCodeEqual(false);
        }
        }
    const handleIdDuplicate = async (e: React.FormEvent) => {
            e.preventDefault();
            try {
                const response = await fetch(`http://aniroomi-env.eba-rj7upyms.ap-northeast-2.elasticbeanstalk.com/auth/id/${userId}/exists`, {
                  method: 'GET',
                  headers: {
                    
                  },
                });
          
                if (response.ok) {
                  //     
                  const data = await response.text();
                  
                //  : true   : false
                   if(data =='true'){
                    setIsIdDuplicate(true)
                   }else{
                    setIsIdDuplicate(false)
                    setIsIdDuplicateButton(1)
                   }
                  
                } else {
                  //    
                  console.error('Error during mailConfirm1:', response.statusText);
                }
              } catch (error) {
                console.error('Error during mailConfirm2:', error);
              }
            };  
    const handleNicknameDuplicate = async (e: React.FormEvent) => {
                e.preventDefault();
                try {
                    const response = await fetch(`http://aniroomi-env.eba-rj7upyms.ap-northeast-2.elasticbeanstalk.com/auth/nickname/${nickname}/exists`, {
                      method: 'GET',
                      headers: {

                      },
                    });
              
                    if (response.ok) {
                      //     
                      const data = await response.text();
                      
                    //  : true   : false
                       if(data =='true'){
                        setIsNicknameDuplicate(true)
                       }else{
                        setIsNicknameDuplicate(false)
                        setIsNicknameDuplicateButton(1)
                       }
                      console.log(data);
                    } else {
                      //    
                      console.error('Error during mailConfirm1:', response.statusText);
                    }
                  } catch (error) {
                    console.error('Error during mailConfirm2:', error);
                  }
                };
    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setStatus(true);
     
        try {
          const response = await fetch('http://aniroomi-env.eba-rj7upyms.ap-northeast-2.elasticbeanstalk.com/api/user', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body:  JSON.stringify({
                userId: userId,
                password: password,
                email: email,
                nickname: nickname,
                gender: gender,
                status: status,
              }),
            credentials: 'include',
          });

          // Handle the response as needed
          console.log(response);

          // Check if login is successful, then redirect to StarPage
          if (response.ok) {
            navigate('/login');
          }
        } catch (error) {
          console.error('Error during login:', error);
        }
      };
      
    const handleEmailChange = (value:string) => {
        setEmail(value);

    };
    const handleCodeChange = (value:string) => {
        setCode(value)
        console.log(code);
    };
    const handleIdChange = (value:string) => {
        setUserId(value)
    };
    const handlePasswordChange = (value:string) => {
        setPassword(value)
    };
    const handlePasswordcheckChange = (value:string) => {
        setPasswordcheck(value)
    };
    const handleNicknameChange = (value:string) => {
        setNickname(value)
    };
    const handleGenderChange = (event: SelectChangeEvent<string>) => {
        setGender(event.target.value);
        console.log(event.target.value);
    };
    const isCauEmail = email.includes('@cau.ac.kr');
    const Idduplicate = isIdDuplicateButton ? "  ." : " "
    const Nicknameduplicate = isNicknameDuplicateButton ? "  ." : " "
    return (
        <div className="flex flex-col w-full h-full items-center justify-center font-['700'] mt-[-20px]">
            <div className="text-2xl text-primary-logo"></div>
            <div className="">Aniroomie   !</div>
            <div className="flex flex-col mt-3">
                {/*   */}
                <div > 
                    {isCauEmail ? 
                        <div className="flex felx-row gap-2">
                            <RegisterTextField
                                error={false}
                                label=" "
                                value={email}
                                onChange={handleEmailChange}
                                helperText=" "
                            /> 
                            <RegisterButton onClick={(e) => handleEmailConfirm(e)}></RegisterButton>
                        </div>

                    :
                    <div className="flex felx-row gap-2">
                        <RegisterTextField
                            error={true}
                            label=" "
                            value={email}
                            onChange={handleEmailChange}
                            helperText="@cau.ac.kr  ."
                            />
                        <RegisterButton onClick={() => {}}></RegisterButton>
                        
                    </div>
                    }
                   
                </div>
                {/*   */}
                 <div className="flex felx-row gap-2">
                    <RegisterTextField
                                error={false}
                                label=" "
                                value={code}
                                onChange={handleCodeChange}
                                helperText=""
                    /> 
    
                    {isCodeEqual == true ? 
                    <div className="flex flex-col items-center text-center mx-auto ">
                      <CheckIcon sx={{color : 'green', width:'35px', height:'35px'}}/>
                      <div className="font-['600'] text-xs">.</div>
                </div>
                    :
                     <RegisterButton onClick={(e) => handleCodeCompare(e)}></RegisterButton>
                }
                    
                </div>
                <div className="font-['400'] text-xs text-gray-400 p-1 ml-2 mb-1">1       </div>
                {/*  */}
                <div className="flex felx-row gap-2">
                     {isIdDuplicate ?
                     <RegisterTextField
                     error={true}
                     label=" "
                     value={userId}
                     onChange={handleIdChange}
                     helperText="  ."
                        />  
                        : 
                    <RegisterTextField
                     error={false}
                     label=" "
                     value={userId}
                     onChange={handleIdChange}
                     helperText={Idduplicate}
                        /> 
                    }
                      
                      <RegisterButton onClick={(e) => handleIdDuplicate(e)}></RegisterButton>
                </div>
                {/*  */}
                <div className="flex felx-row gap-2 mb-3 mt-1">
                    <PasswordTextField
                                error={false}
                                label=" "
                                value={password}
                                onChange={handlePasswordChange}
                                helperText=""
                    /> 
                </div>
                {/*   */}
                <div >
                     {password === passwordcheck ?
                    <div className="flex felx-row gap-2">
                        <PasswordTextField
                    error={false}
                    label=" "
                    value={passwordcheck}
                    onChange={handlePasswordcheckChange}
                    helperText=" "
        /> 
                    
                    </div>
                     :
                     <div className="flex felx-row gap-2" >
                        <PasswordTextField
                                error={true}
                                label=" "
                                value={passwordcheck}
                                onChange={handlePasswordcheckChange}
                                helperText="  ."
                    /> 
                   
                     </div>
                      }
    
                     
                </div>
                {/* */}
                <div className="flex felx-row gap-2">
                {isNicknameDuplicate ?
                     <RegisterTextField
                     error={true}
                     label=" "
                     value={nickname}
                     onChange={handleNicknameChange}
                     helperText="  ."
                        />  
                        : 
                    <RegisterTextField
                     error={false}
                     label=" "
                     value={nickname}
                     onChange={handleNicknameChange}
                     helperText={Nicknameduplicate}
                        /> 
                    }
                      <RegisterButton onClick={(e) => handleNicknameDuplicate(e)}></RegisterButton>
                </div> 
                {/* */}
                <div className="flex felx-row gap-2 p-1 justify-evenly items-center mb-1">
                    <div className="font-['700'] text-xl"></div>        
                    <FormControl sx={{ width: '200px',borderRadius: '50%' }}>
                        <InputLabel id="demo-simple-select-label" ></InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={gender}
                            label="Age"
                            onChange={handleGenderChange}
                            sx={{
                                borderRadius: '10px',
                                height : '48px',
                            }}
                            
                        >
                            <MenuItem value={'F'}></MenuItem>
                            <MenuItem value={'M'}></MenuItem>
                        </Select>
                        </FormControl>
                
                    </div>
                </div>
                <Button onClick={(e) => handleSubmit(e)}> </Button>
                
            </div>
         
    )
}